define('powerbi-embedded-site/adapters/workspace', ['exports', 'powerbi-embedded-site/adapters/subscription'], function (exports, _powerbiEmbeddedSiteAdaptersSubscription) {
  exports['default'] = _powerbiEmbeddedSiteAdaptersSubscription['default'].extend({
    /**
     * Not sure why this is needed? but without it, headers are overwritten with base adapters headers
     * findLink > findHasMany > this.ajax > this.ajaxOptions > this.headers() (The onBeforeSend functions set headers result on top of options)
     */
    headers: function headers() {
      console.log('workspace#headers');

      return {
        'Authorization': 'AppToken xyz'
      };
    },

    findHasMany: function findHasMany(store, snapshot, url /*, relationship*/) {
      var _this = this;

      console.log('workspace#findHasMany');
      var id = snapshot.id;
      var type = snapshot.modelName;

      url = this.urlPrefix(url, this.buildURL(type, id, snapshot, 'findHasMany'));

      return snapshot.record.get('token').then(function (token) {
        return _this.ajax(url, 'GET', {
          headers: {
            'Authorization': 'AppToken ' + token
          }
        });
      });
    }
  });
});